"use strict";
import platform from "../imgs/platform.png";
import platform2 from "../imgs/platform2.png";
import hills from "../imgs/hills.png";
import hills2 from "../imgs/hills2.png";
import hills4 from "../imgs/hills4.png";
import background from "../imgs/background.png";
import background2 from "../imgs/background2.png";
import spriteStandLeft from "../imgs/spriteStandLeft.png";
import spriteStandLeft2 from "../imgs/spriteStandLeft2.png";
import spriteStandRight from "../imgs/spriteStandRight.png";
import spriteStandRight2 from "../imgs/spriteStandRight2.png";
import spriteRunLeft from "../imgs/spriteRunLeft.png";
import spriteRunLeft2 from "../imgs/spriteRunLeft2.png";
import spriteRunRight from "../imgs/spriteRunRight.png";
import spriteRunRight2 from "../imgs/spriteRunRight2.png";
import platformSmallTall from "../imgs/platformSmallTall.png";
import platformSmallTall2 from "../imgs/platformSmallTall2.png";

const canvas = document.querySelector("canvas");

canvas.width = 1350;
canvas.height = 660;

const c = canvas.getContext("2d");

const gravity = 1;

class Player {
  constructor() {
    this.speed = 15;
    this.width = 65;
    this.height = 150;
    this.color = "red";
    this.position = {
      x: 100,
      y: 100,
    };
    this.velocity = {
      x: 0,
      y: 0,
    };
    this.image = createImage(spriteStandRight);
    this.frames = 0;
    this.sprites = {
      stand: {
        right: createImage(spriteStandRight),
        left: createImage(spriteStandLeft),
        cropWidth: 177,
        width: this.width,
      },
      run: {
        right: createImage(spriteRunRight),
        left: createImage(spriteRunLeft),
        cropWidth: 341,
        width: 127.875,
      },
    };
    this.currentSprite = this.sprites.stand.right;
    this.currentCropWidth = this.sprites.stand.cropWidth;
  }
  draw() {
    // c.fillStyle = this.color;
    c.drawImage(
      this.currentSprite,
      this.currentCropWidth * this.frames,
      0,
      this.currentCropWidth,
      400,
      this.position.x,
      this.position.y,
      this.width,
      this.height
    );
    // c.fillRect(this.position.x, this.position.y, this.width, this.height);
  }

  update() {
    this.frames++;
    if (this.frames > 59 && this.currentSprite === player.sprites.stand.right) {
      this.frames = 0;
    } else if (
      this.frames > 29 &&
      this.currentSprite === player.sprites.run.right
    ) {
      this.frames = 0;
    }
    if (this.frames > 59 && this.currentSprite === player.sprites.stand.left) {
      this.frames = 0;
    } else if (
      this.frames > 29 &&
      this.currentSprite === player.sprites.run.left
    ) {
      this.frames = 0;
    }
    this.draw();
    this.position.y += this.velocity.y;
    this.position.x += this.velocity.x;
    if (this.position.y + this.height + this.velocity.y <= canvas.height) {
      this.velocity.y += gravity;
    }
  }
}

class Platform {
  constructor({ x, y, image }) {
    this.position = {
      x,
      y,
    };
    this.width = image.width;
    this.height = image.height;
    this.color = "blue";
    // this.draw();

    this.image = image;
    console.log(image);
  }

  draw() {
    // c.fillStyle = this.color;
    c.drawImage(this.image, this.position.x, this.position.y);
  }
}

class GenericObject {
  constructor({ x, y, image }) {
    this.position = {
      x,
      y,
    };
    this.width = image.width;
    this.height = image.height;
    console.log(this.width, this.height);
    this.color = "blue";
    // this.draw();

    this.image = image;
  }

  draw() {
    // c.fillStyle = this.color;
    c.drawImage(
      this.image,
      this.position.x,
      this.position.y,
      this.width,
      this.height
    );
  }
}

function createImage(imageSrc) {
  const image = new Image();
  if (imageSrc == hills2) {
    image.width = 7545;
    image.height = 592;
  }
  if (imageSrc == background2) {
    image.width = 11643;
    image.height = 732;
  }
  image.src = imageSrc;
  return image;
}

function platformSmallTallImage(imageSrc) {
  const image = new Image();
  image.src = imageSrc;
  return image;
}

let platformImage = createImage(platform);

let player = new Player();
let platforms = [];
let genericObjects = [];

let lastKey;
let keys = {
  right: {
    pressed: false,
  },
  left: {
    pressed: false,
  },
};

let scrollOffset = 0;

function init() {
  platformImage = createImage(platform2);

  player = new Player();
  platforms = [
    new Platform({
      x: platformImage.width * 4 + 7 + platformImage.width,
      y: 350,
      image: platformSmallTallImage(platformSmallTall2),
    }),
    new Platform({
      x: platformImage.width * 7 + 500 + platformImage.width,
      y: 350,
      image: platformSmallTallImage(platformSmallTall2),
    }),
    new Platform({
      x: platformImage.width * 10 + 7 + platformImage.width,
      y: 350,
      image: platformSmallTallImage(platformSmallTall2),
    }),
    new Platform({
      x: platformImage.width * 13 + 7 + platformImage.width,
      y: 350,
      image: platformSmallTallImage(platformSmallTall2),
    }),
    new Platform({
      x: -1,
      y: 535,
      image: platformImage,
    }),
    new Platform({
      x: platformImage.width - 3,
      y: 535,
      image: platformImage,
    }),
    new Platform({
      x: platformImage.width * 2 + 100,
      y: 535,
      image: platformImage,
    }),
    new Platform({
      x: platformImage.width * 3 + 300,
      y: 535,
      image: platformImage,
    }),
    new Platform({
      x: platformImage.width * 4 + 300 - 2,
      y: 535,
      image: platformImage,
    }),
    new Platform({
      x: platformImage.width * 5 + 650,
      y: 535,
      image: platformImage,
    }),
    new Platform({
      x: platformImage.width * 6 + 850,
      y: 535,
      image: platformImage,
    }),
    new Platform({
      x: platformImage.width * 7 + 1050,
      y: 535,
      image: platformImage,
    }),
    new Platform({
      x: platformImage.width * 8 + 1250,
      y: 535,
      image: platformImage,
    }),
    new Platform({
      x: platformImage.width * 9 + 1650,
      y: 535,
      image: platformImage,
    }),
    new Platform({
      x: platformImage.width * 10 +1950,
      y: 535,
      image: platformImage,
    }),
    new Platform({
      x: platformImage.width * 11 + 2050,
      y: 535,
      image: platformImage,
    }),
    new Platform({
      x: platformImage.width * 12 + 2300,
      y: 535,
      image: platformImage,
    }),
    new Platform({
      x: platformImage.width * 13 + 2600,
      y: 535,
      image: platformImage,
    }),
    new Platform({
      x: platformImage.width * 14 + 3000,
      y: 535,
      image: platformImage,
    }),
  ];
  genericObjects = [
    new GenericObject({
      x: -1,
      y: -1,
      image: createImage(background),
    }),
    new GenericObject({
      x: 0,
      y: 0,
      image: createImage(hills2),
    }),
  ];

  scrollOffset = 0;
}

const animate = function () {
  requestAnimationFrame(animate);
  c.fillStyle = "white";
  c.fillRect(0, 0, canvas.width, canvas.height);

  genericObjects.forEach((object) => {
    object.draw();
  });

  platforms.forEach((platform) => {
    platform.draw();
  });
  player.update();

  if (keys.right.pressed && player.position.x < 400) {
    player.velocity.x = player.speed;
  } else if (
    (keys.left.pressed && player.position.x > 100) ||
    (keys.left.pressed && scrollOffset === 0 && player.position.x > 0)
  ) {
    player.velocity.x = -player.speed;
  } else {
    player.velocity.x = 0;
    if (keys.right.pressed) {
      scrollOffset += player.speed;
      platforms.forEach((platform) => {
        platform.position.x -= player.speed;
      });
      genericObjects.forEach((genericObject) => {
        genericObject.position.x -= player.speed * 0.66;
      });
    } else if (keys.left.pressed && scrollOffset > 0) {
      scrollOffset -= player.speed;

      platforms.forEach((platform) => {
        platform.position.x += player.speed;
      });

      genericObjects.forEach((genericObject) => {
        genericObject.position.x += player.speed * 0.66;
      });
    }
  }

  platforms.forEach((platform) => {
    if (
      player.position.y + player.height <= platform.position.y &&
      player.position.y + player.height + player.velocity.y >=
        platform.position.y &&
      player.position.x + player.width >= platform.position.x &&
      player.position.x <= platform.position.x + platform.width
    ) {
      player.velocity.y = 0;
    }
  });

  //sprite switching condition
  if (
    keys.right.pressed &&
    lastKey === "right" &&
    player.currentSprite !== player.sprites.run.right
  ) {
    player.frames = 1;
    player.currentSprite = player.sprites.run.right;
    player.currentCropWidth = player.sprites.run.cropWidth;
    player.width = player.sprites.run.width;
  } else if (
    keys.left.pressed &&
    lastKey === "left" &&
    player.currentSprite !== player.sprites.run.left
  ) {
    player.currentSprite = player.sprites.run.left;
    player.currentCropWidth = player.sprites.run.cropWidth;
    player.width = player.sprites.run.width;
  } else if (
    !keys.right.pressed &&
    lastKey === "right" &&
    player.currentSprite !== player.sprites.stand.right
  ) {
    player.currentSprite = player.sprites.stand.right;
    player.currentCropWidth = player.sprites.stand.cropWidth;
    player.width = player.sprites.stand.width;
  } else if (
    !keys.left.pressed &&
    lastKey === "left" &&
    player.currentSprite !== player.sprites.stand.left
  ) {
    player.currentSprite = player.sprites.stand.left;
    player.currentCropWidth = player.sprites.stand.cropWidth;
    player.width = player.sprites.stand.width;
  }

  if (scrollOffset > platformImage.width * 5 + 250) {
    console.log("you win");
  }

  if (player.position.y > canvas.height) {
    console.log("you lose");
    init();
  }
};
init();
animate();

addEventListener("keydown", ({ keyCode }) => {
  switch (keyCode) {
    case 65:
      keys.left.pressed = true;
      lastKey = "left";
      break;

    case 68:
      keys.right.pressed = true;
      lastKey = "right";
      break;

    case 87:
      player.velocity.y = -25;
      break;
    case 83:
      player.velocity.y += 0;
      break;
  }
});
addEventListener("keyup", ({ keyCode }) => {
  switch (keyCode) {
    case 65:
      keys.left.pressed = false;
      break;

    case 68:
      keys.right.pressed = false;
      break;

    case 87:
      player.velocity.y = 0;
      break;

    case 83:
      player.velocity.y += 0;
      break;
  }
});

